import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { DeliveryService } from '../../services/delivery/delivery.service'

@Component({
  selector: 'app-list-delivery',
  templateUrl: './list-delivery.component.html',
  styleUrls: ['./list-delivery.component.scss'],
})
export class ListDeliveryComponent implements OnInit {

  @Output() requestSelect = new EventEmitter;

  requests=[];
  listResponse =[];

  constructor(private serviceDelivery: DeliveryService) { }

  ngOnInit() {
    this.serviceDelivery.listAllDelivery().subscribe(
      (res:any)=>{
        console.log(res);
        res.forEach(element => {
          let response ={
            _id: element._id,
            clientId: element.clientId,
            date: element.date,
            destiny: element.destiny,
            distance: 1,
            origin: element.origin,
            product: element.product,
            status: element.status,
            userId: element.userId,
            value: parseInt(element.value),
          }
         // if(element.status == 'En Espera'){            
            this.requests.push(response)
         // }
        });

    },(err)=>{
        console.log(err);        
    })
  }

  idRequestSelect(idSelect){
    this.requestSelect.emit(idSelect)   
  }

}
